.elementor-widget-wdb--counter .wdb--counter {
  gap: 20px;
  display: flex;
  flex-direction: column;
}
.elementor-widget-wdb--counter .wdb--counter .count {
  line-height: 1;
  font-size: 36px;
  font-weight: 500;
}
.elementor-widget-wdb--counter .wdb--counter .title {
  margin: 0;
}
.elementor-widget-wdb--counter .wdb--counter .separator {
  background-color: #ddd;
}
.elementor-widget-wdb--counter.wdb-position-row .count,
.elementor-widget-wdb--counter.wdb-position-row .title {
  align-self: center;
}
.elementor-widget-wdb--counter.wdb-position-row .separator {
  height: auto;
  width: 1px;
}
.elementor-widget-wdb--counter.wdb-position-column .separator {
  height: 1px;
  width: 100%;
}
